.jkl-content-toggle{height:1.2em;overflow:hidden;display:block}.jkl-content-toggle--show-first{-webkit-transform:translateY(0);transform:translateY(0)}.jkl-content-toggle--show-second{-webkit-transform:translateY(-50%);transform:translateY(-50%)}.jkl-content-toggle__slider{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;transition-property:-webkit-transform;transition-property:transform;transition-property:transform,-webkit-transform;transition-timing-function:ease;transition-duration:.25s}@media (prefers-reduced-motion){.jkl-content-toggle__slider{transition-property:none}}